/* @flow */

import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  background: #f2ce94;
`;

type Props = {};

class Component extends React.Component<Props> {
  render() {
    return (
      <Container>
        {this.props.children}
      </Container>
    );
  }
}

export default Component;



